*,html,body,header,main,footer,div,p,h1,h2,h3,h4,ul,li{box-sizing:border-box;padding:0;margin:0}a{text-decoration:none;color:inherit}html{font-family:"Roboto", sans-serif;font-size:10px}.backBtn{width:100vw;padding:20px 0}.backBtn>div{width:80vw;max-width:780px;margin:0 auto;display:flex;justify-content:space-between;align-items:center}.backBtn>div a{display:block;font-size:1.4rem;position:relative}.backBtn>div a::before{display:block;content:"";width:60px;height:10px;background:#ffde00;position:absolute;bottom:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);z-index:-1}

*,html,body,header,main,footer,div,p,h1,h2,h3,h4,ul,li{box-sizing:border-box;padding:0;margin:0}a{text-decoration:none;color:inherit}html{font-family:"Roboto", sans-serif;font-size:10px}.login{width:80vw;max-width:780px;margin:0 auto;padding-top:100px;min-height:calc(100vh - 45px - 67px)}.login .login__title{margin-bottom:30px;font-size:3.8rem;font-weight:800;text-transform:uppercase}.login form input,.login form button{all:unset;display:block;width:280px;padding:7px 0px;margin-bottom:10px;border-bottom:1px solid black;font-size:1.4rem}.login form input:nth-child(2),.login form button:nth-child(2){margin-bottom:30px}.login form button{background:black;font-size:1.2rem;color:white;text-align:center}

